@mixin commonStyle() {
    margin-top: 20px;
    position: relative;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
    width: 350px; }

.danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
    @include commonStyle(); }

.warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba;
    @include commonStyle(); }
